<template>
  <div id="app">

    <div class="progress-container">
      <div class="progress-bar" :style="{ width: progress + '%' }"></div>
    </div>

    <div class="layout-container">

      <div class="sidebar">
        <Header />
        <Nav />
      </div>
      
      <header class="mobile-header">
        <Header />
      </header>

      <main class="content">
        <div class="content-wrapper">  
          <router-view />
        </div>
      </main>
      
      <nav class="mobile-nav">
        <Nav />
      </nav>
      <footer class="footer">
        <Footer />
      </footer>
    </div>
<a href="https://github.com/qiiizhen" 
   class="github-corner" 
   aria-label="View homepage on GitHub">
        <svg width="80" height="80" viewBox="0 0 250 250" style="fill:#151513; color:#fff; position: absolute; top: 0; border: 0; right: 0;">
            <path d="M0,0 L115,115 L130,115 L142,142 L250,250 L250,0 Z"></path>
            <path d="M128.3,109.0 C113.8,99.7 119.0,89.6 119.0,89.6 C122.0,82.7 120.5,78.6 120.5,78.6 C119.2,72.0 123.4,76.3 123.4,76.3 C127.3,80.9 125.5,87.3 125.5,87.3 C122.9,97.6 130.6,101.9 134.4,103.2" fill="currentColor" style="transform-origin: 130px 106px;" class="octo-arm"></path>
            <path d="M115.0,115.0 C114.9,115.1 118.7,116.5 119.8,115.4 L133.7,101.6 C136.9,99.2 139.9,98.4 142.2,98.6 C133.8,88.0 127.5,74.4 143.8,58.0 C148.5,53.4 154.0,51.2 159.7,51.0 C160.3,49.4 163.2,43.6 171.4,40.1 C171.4,40.1 176.1,42.5 178.8,56.2 C183.1,58.6 187.2,61.8 190.9,65.4 C194.5,69.0 197.7,73.2 200.1,77.6 C213.8,80.2 216.3,84.9 216.3,84.9 C212.7,93.1 206.9,96.0 205.4,96.6 C205.1,102.4 203.0,107.8 198.3,112.5 C181.9,128.9 168.3,122.5 157.7,114.1 C157.9,116.9 156.7,120.9 152.7,124.9 L141.0,136.5 C139.8,137.7 141.6,141.9 141.8,141.8 Z" fill="currentColor" class="octo-body"></path>
        </svg>
  </a>

  </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue' 
import { nextTick } from 'vue' 
import { useRouter } from 'vue-router'
import Header from './components/Header.vue'
import Nav from './components/Nav.vue'
import Footer from './components/Footer.vue'
import * as Vue from 'vue'
console.log('Vue exports:', Vue) 

export default {
  name: 'App',
  components: {
    Header,
    Nav,
    Footer,
  },
  setup() {
    const progress = ref(0)

    const updateProgress = () => {
      const scrollTop = window.scrollY
      const docHeight = document.documentElement.scrollHeight - window.innerHeight
      const scrolled = (scrollTop / docHeight) * 100
      progress.value = Math.min(100, Math.max(0, scrolled))
    }

    onMounted(() => {
      window.addEventListener('scroll', updateProgress)
      updateProgress()
    })

    onUnmounted(() => {
      window.removeEventListener('scroll', updateProgress)
    })

    const router = useRouter()
    router.afterEach(() => {
      progress.value = 0
      window.scrollTo(0, 0)
      nextTick(() => {
        updateProgress()
      })
    })

    return { progress }
  }
}

</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 60px;
}

.layout-container {
  display: grid;
  grid-template-areas:
    "sidebar content"
    "footer footer";
    grid-template-columns: 200px 1fr;
  margin-left: 3rem;
  gap: 8rem;
  padding:1rem;
  padding-right: 7rem;

}

.sidebar {
  grid-area: sidebar;
  width: 200px; 
  border-left: 60px;
  position: fixed;
  top: 80px;
  
}

.content-wrapper {
  max-width: 1200px; 
  width: 100%;
  margin: 0 auto;
}

.content {
  background-color:rgba(143, 143, 135, 0.075);
  border-radius: 3px;
  grid-area: content;
  flex: 1;
  padding: 1rem;
  min-width: 0;
  max-width:calc(100%-200px);
}

.footer{
  grid-area: footer;
  padding: 1rem;
  text-align:center;
  align-items: center;
  justify-content: center;
  grid-column: 1 / -1; 
}

.progress-container {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 4px;
      background: transparent;
      z-index: 1000;
    }
    
    .progress-bar {
      height: 100%;
      background: linear-gradient(90deg, #c5eddd, #9dc3f7);
      width: 0%;
      transition: width 0.2s ease;
    }

.mobile-header,
.mobile-nav {
  display: none; 
}

@media (max-width: 768px) {
  .layout-container {
    display: flex;
    flex-direction: column;
    gap:1rem;
  }

  .sidebar{
    display: none;
  }
   .mobile-header {
    display: block;
    order:1;
    text-align: left;
    width:fit-content
  }
  
  .mobile-nav {
    display: block;
    order: 2; 
  }
  
  .content {
    order: 3;
  }
}

Footer {
  order:4;
  margin-top: auto;
}

.github-corner:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out;
}

@keyframes octocat-wave {
  0%, 100% { transform: rotate(0); }
  20%, 60% { transform: rotate(-25deg); }
  40%, 80% { transform: rotate(10deg); }
}

@media (max-width: 500px) {
  .github-corner:hover .octo-arm {
    animation: none; 
  }
}
</style>
